<template>
	<input
		type="text"
		class="m-2"
		:class="size"
		:value="modelValue"
		:placeholder="placeholder"
		@input="updateInput"
	/>
</template>



<script>
export default {
	name: "MyInput",
	props: {
		modelValue: {
			type: String,
			default: "",
		},
		size: {
			type: String,
			default: "form-control",
		},
		placeholder: {
			type: String,
			default: "",
		},
	},
	emits: ["update:modelValue"],
	//emits - @input="updateInput" ,  modelValue (  input  , )
	//    v-model.trim="inputValue" ; inputValue      data !
	// :value="modelValue" -  ,   !
	methods: {
		updateInput(event) {
			this.$emit("update:modelValue", event.target.value);
		},
	},
};
</script>

